*
{
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}
body
{
    background: url(github.webp);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
}
.hero
{
    text-align: center;
    color: whitesmoke;
}
.hero input
{
    margin-top: 30px;
    padding: 15.5px 10px;
    border-radius: 5px;
    border: none;
    outline: none;
}
.hero button
{
    padding: 15px 20px;
    background: green;
    color: whitesmoke;
    font-size: 15.5px;
    font-weight: bolder;
    cursor: pointer;
    border: none;
    outline: none;
    margin-top: 10px;
    border-radius: 5px;
}
nav
{
    display: flex;
    background-color: white;
    justify-content: center;
    padding: 25px 0;
    margin-top: 10px;
    background: linear-gradient(90deg,#78d7da 0%, #edd1d7 80%);
}
nav ul
{
    display: flex;
    justify-content: space-around;
    align-items: center;
    list-style-type: none;
    width: 75%;
    cursor: pointer;
    font-size: 25px;
    color: darkred;
    font-weight: bolder;
    transition: all 0.5s ease;
}
nav ul li:hover
{
    transform: scale(1.3);
}
.navActive
{
    color: black;
    font-size: 30px;
    font-weight: bold;
}
.answersec
{
    background: whitesmoke;
    padding: 15px;
    font-size: 15px;
}